
#backHome{
    background-color: white;
    color: #1abc9c;
    border: 1px solid #1abc9c;
    border-radius: 4px;
    box-sizing: border-box;
    padding: 10px;
    letter-spacing: 1px;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    min-width: 140px;
    margin-top: 8px;
    cursor: pointer;

    text-transform: uppercase;
    transition: 0.1s all;
    font-size: 10px;
    outline: none;
    position: relative;
    bottom: 70px;
    right: 140px;
}
.box{
    width: 80%;
    margin-left: 10%;
    box-shadow: 0 0 0 white;
    background: white;
    overflow: hidden;
}
.box input{
    font-size: 14px !important;
}
#AddPokemon{
    margin-top: 6%;
    margin-bottom: 6%;
    font-size: 14px;
}
#backHome:hover{
    background-color: #1abc9c;
    color: white;
    border: 1px solid #1abc9c;
    border-radius: 4px;
    box-sizing: border-box;
    padding: 10px;
    letter-spacing: 1px;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    min-width: 140px;
    margin-top: 8px;
    cursor: pointer;

    text-transform: uppercase;
    transition: 0.1s all;
    font-size: 10px;
    outline: none;
}
#backHome:hover a{
    color: white;
}
#AddPokemon .box {
    height: auto;
}
.glyphicon-remove {
    font-size: 20px !important;
    margin: 5px;
}
ul.type {
    font-size: 16px;
    margin-bottom: 30px;
}
ul.type li {
    margin: 5px;
    list-style-type: none;
}
ul.evolution {
    font-size: 16px;
}
ul.evolution li {
    font-size: 16px;
    list-style-type: none;
}



